import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

Vue.use(Vuex)

let store = new Vuex.Store({
  state: {
    cookie: false,
    originaImg: {},
    chartType: [
      {
        value: '1',
        label: '柱状图'
      },
      {
        value: '3',
        label: '饼图'
      }
    ],
    StigmataType: [
      {
        value: '100',
        label: '叶卡捷琳娜'
      },
      {
        value: '110',
        label: '莎士比亚'
      },
      {
        value: '120',
        label: '克列欧博克拉'
      },
      {
        value: '130',
        label: '姬轩辕'
      },
      {
        value: '140',
        label: '普朗克'
      },
      {
        value: '158',
        label: '李耳'
      },
      {
        value: '160',
        label: '女娲'
      },
      {
        value: '170',
        label: '伏羲'
      },
      {
        value: '180',
        label: '伊萨克.牛顿'
      },
      {
        value: '190',
        label: '薛定谔'
      },
      {
        value: '200',
        label: '李耳红'
      },
      {
        value: '210',
        label: '卡莲.誓约'
      },
      {
        value: '220',
        label: '真田幸村'
      },
      {
        value: '230',
        label: '齐格飞.卡斯兰娜'
      },
      {
        value: '240',
        label: '塞西莉亚.沙尼亚特'
      },
      {
        value: '250',
        label: '浓姬'
      },
      {
        value: '316',
        label: '神农'
      }
    ]
  },
  mutations: {
    changecookie(state, data) {//设置cookie(登录持久化)
      state.cookie = data
    },
    originalImgWH(state, data) {// 原始图片信息
      state.originaImg.width = data.width
      state.originaImg.height = data.height
      state.originaImg.src = data.src
      console.log(state)
    }
  },
  actions: {
    getcookie(context) {
      Axios.get('/cookie', context.state.cookie)
      .then(function(res) {
        context.commit('changecookie', res.data)
      })
      .catch(function(err) {
        console.log(err)
      })
    }
  }
})

export default store



