<template>
  <div>
    <div id="wrap">
      <h1>{{h1}}</h1>
      <h2>{{h2}}</h2>
      <p>
        <router-link to='/kiana'>{{kiana}}</router-link>
      </p>
      <p>
        <router-link to='/bronya'>{{bronya}}</router-link>
      </p>
      <p>
        <router-link to='/mei'>{{mei}}</router-link>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      h1: 'AXWE',
      h2: 'Maybe Nothing',
      kiana: '强袭，强无敌',
      bronya: '随便玩玩',
      mei: '随便看看'
    }
  }
}
</script>

<style scoped>
#wrap {
  font-family: "微软雅黑";
  position: absolute;
  left: 0;
  width: 320px;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -160px;
  margin-top: -160px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  z-index: 1;
}

#wrap h1{
  height: 42px;
  margin: 21px 0;
  font-size: 38px;
}

#wrap h2{
  height: 31px;
  margin: 20px 0;
  font-size: 25px;
}

#wrap p{
  height: 21px;
  margin: 16px 0
}

a {
  text-decoration: none;
  transition: .3s;
  color: #999;
  font-size: 14px;
}

a:hover {
  color: red;
}
</style>



