<template>
  <div class="wrap">
    <div @click="initCanvas()">
      111111111111111111111111111
    </div>
    <div class="c">
      <!-- <div class="top">
        <div class="nd">
          <div class="nd-img" :style="{backgroundImage:nd}"></div>
        </div>
        <div class="st">
          <div class="st-img" :style="{backgroundImage:st}"></div>
        </div>
        <div class="rd">
          <div class="rd-img" :style="{backgroundImage:rd}"></div>
        </div>
      </div> -->
      <div id="echarts"></div>
      <div class="bt">
        <Button 
          type="ghost" 
          icon="ios-information-outline"
          @click="detailed = true"
        >查看详细</Button>
        <Upload 
          action="http://www.axwe.wang:8088/api/upload" 
          accept="image/png,image/jpge,image/jpg"
          name="miaov"
          with-credentials="true"
          :format="uploadType"
          :on-success="uploadSuccess"
          :before-upload="beforeUpload"
        >
            <Button type="ghost" icon="ios-cloud-upload-outline">上传我的</Button>
        </Upload>
      </div>
    </div>
    <div class="xx">
      <Modal
        v-model="detailed">
        <div slot="header">
          <Select v-model="model10" multiple style="width:316px">
            <Option v-for="item in StigmataType" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <div>
          111
        </div> 
        <div slot="footer">
          <Select v-model="model1" style="width:158px">
            <Option v-for="item in chartType" 
              :value="item.value" 
              :key="item.value"
              placeholder="图表类型"
            >{{ item.label }}</Option>
          </Select>
          <DatePicker type="daterange" 
            placeholder="选择日期" 
            style="width: 158px"
            :options="optionsDates"
          ></DatePicker>
        </div>
      </Modal>
    </div>
    <img :src="userimage">
    <canvas id="originalImg"></canvas>
    <canvas id="blackWhiteImg"></canvas>
    <canvas id="showImg"></canvas>
  </div>
</template>

<script>
  import rd3 from '@/assets/7.png'
  import rd1 from '@/assets/8.png'
  import rd2 from '@/assets/9.png'
  export default {
    data() {
      return {
        originalImg2d: '',
        // blackWhiteImg2d: '',
        // showImg2d: '',
        // userimage: '',
        st:`url(${rd3})`,
        nd:`url(${rd2})`,
        rd:`url(${rd1})`,
        detailed: false,
        //date: this.$store.state.date,
        //time: this.$store.state.time,
        chartType: this.$store.state.chartType,
        StigmataType: this.$store.state.StigmataType,
        uploadType: ['png', 'jpg', 'jpge'],
        optionsDates: {
          disabledDate (date) {
            return date > Date.now()
          }
        }
      }
    },
    mounted() {
      this.resizeCharts()
    },
    methods: {
      uploadSuccess(response, file, fileList) { //上传成功
        this.userimage = response
        this.initCanvas()
      },
      beforeUpload() { //开始上传前
        console.log('really go')
      },
      resizeCharts() {
        let myEchart = document.getElementById('echarts')
        let chartBox = document.getElementsByClassName('c')[0]
        myEchart.style.width = chartBox.offsetWidth
        this.initEcharts(myEchart)
      },
      //字符云
      initEcharts(element) {
        let myEchart = this.$echarts.init(element)
        myEchart.setOption({
          series: [
            {
              name: '来源',
              type: 'wordCloud',
              shape: 'smooth',
              rotationRange: [46, 80],
              sizeRange: [12, 60],
              rotationStep: 45,
              gridSize: 8,
              drawOutfBound: false,
              textStyle: {
                normal: {
                  fontFamily: 'sans-serif',
                  fontWeight: 'bold',
                  color: () => {
                    return 'rgb(' + [
                      Math.round(Math.random()* 160),
                      Math.round(Math.random()* 160),
                      Math.round(Math.random()* 160)
                    ].join(',')+')'
                  }
                }
              },
              emphasis: {
                shadowBlur: 10,
                shadowColor: '#333'
              },
              data: [
                {value: 99, name: '1'},
                {value: 233, name: '2'},
                {value: 2333, name: '3'},
                {value: 2, name: '4'},
                {value: 23, name: '5'},
              ]
            }
          ]
        })
      },
      initCanvas() {//初始化画布
        let src = 'http://www.axwe.wang:8088/' + this.userimage
        let img = new Image()
        img.src = src
        console.log(src)
        //获取canvas元素
        let originalImg = document.getElementById('originalImg')
        let blackWhiteImg = document.getElementById('blackWhiteImg')
        let showImg = document.getElementById('showImg')
        //设置宽高
        img.onload = () => {
          originalImg.width = img.width
          originalImg.height = img.height
          blackWhiteImg.width = img.width
          blackWhiteImg.height = img.height
          showImg.width = img.width
          showImg.height = img.height
          //记录图片宽高
          this.$store.commit('originalImgWH', {width:img.width, height:img.height, src:img.src})
          //获取context对象
          this.originalImg2d = originalImg.getContext('2d')
          this.blackWhiteImg2d = blackWhiteImg.getContext('2d')
          this.showImg2d = showImg.getContext('2d')
          //绘制原始图
          this.originalImg2d.drawImage(img, 0, 0)
          originalImg.style.display = 'block'
          this.getOriginalData(this.originalImg2d, img.width, img.height)
        }
      },
      //获取图片像素信息并二值化
      getOriginalData(elementContext, width, height) {//元素文本， 宽， 高
        let data1 = elementContext.getImageData(0, 0, width, height)
        let data11 = data1.data
        this.toBW(data11)
        //elementContext.putImageData(data1, 0, 0)
        this.getFilterPoint(data11, width, height)
      },
      //特征点
      getFilterPoint(data, width, height) {//像素数据， 宽， 高
        let arrPoint = [] 
        for(let i = 0; i < data.length; i+=4) {
          arrPoint.push(data[i])
        }
        let arrX = this.toTwo(arrPoint, width) //得到二维数组
        let whiteNumObj = this.getWhiteNum(arrX, width) //得到X轴为单位的点数量对象
        let xxx = this.tezhenxian(whiteNumObj)
        console.log(xxx)
        this.huihua(whiteNumObj[xxx].arr, xxx)
      },
      toGary(arr) {//把RGBA信息转换成灰度信息
        let i = 0;
        let arr2 = []
        while (i < arr.length) {
          arr2.push((arr[i] * 299 + arr[i + 1] * 587 + arr[i + 2] * 114 + 500) / 1000)
          i += 4
        }
        return arr2
      },
      toBW(data) {//把RGBA信息转换成黑白信息
        let i = 0
        while (i < data.length) {
          let avg = (data[i] * 299 + data[i + 1] * 587 + data[i + 2] * 114 + 500) / 1000
          avg < 200 ? avg = 0 : avg = 255
          data[i] = avg
          data[i + 1] = avg
          data[i + 2] = avg
          i += 4
        }
      },
      toTwo(arr, width) {//把数组转换成二维数组，对应每一个像素点
        let arr2 = []
        while (arr.length) arr2.push(arr.splice(0, width))
        return arr2
      },
      getWhiteNum(arr, width) {//寻找white数量，以及改变点
        let arr2 = [] 
        for(let i = 0;i<arr.length;i++){
          let obj = {
            num:0,
            arr:[]
          }
          arr[i].forEach(function(item,index,arr){
            if(index === width-1) return  //跳过最后一个点
            if(item > 0 ) obj.num++       //记录白点数量
            if(item != arr[index+1]){
              obj.arr.push(index)         //记录变色点的坐标
            }
          })
          arr2.push(obj)
        }
        return arr2
      },
      filter(arr) {
        let arr2 = []
        arr.forEach(function(element,index) {
          if(element.arr.length === 10){
            arr2.push(index)
          }
        })
      },
      tezhenxian(arr){
        let arr2 = []
        arr.forEach((item, index) => {
          if(item.arr.length == 10) arr2.push(index)
        })
        for(let i = 0;i<arr2.length;i++){
          if(arr2[i]+3 === arr2[i+3]) return arr2[i]
        }
      },
      huihua(arr, xxx) {
        console.log(arr)
        let width = arr[1] - arr[0]
        for(let i = 0; i<arr.length; i+=2) {
          this.originalImg2d.strokeRect(arr[i], xxx-width, width, width)
        }
      }
    }
  }
</script>

<style scoped>
  .wrap{
    position: absolute;
    height: 100%;
    width: 100%;
  }
  .c{
    width: 66%;
    height: 540px;
    position: absolute;
    margin: 8% 17% 0 17%;
  }
  .top{
    width: 100%;
    height: 100%;
    height: 540px;
    position: absolute;
  }
  .st{
    left: 33.3%;
    width: 33.3%;
    height: 540px;
    background: #f0f;
    overflow: hidden;
    position: absolute;
  }
  .nd{
    margin-top: 108px;
    width: 33.3%;
    height: 432px;
    background: #ff0;
    overflow: hidden;
    position: absolute;
  }
  .rd{
    margin-top: 216px;
    width: 33.3%;
    height: 324px;
    background: #0ff;
    overflow: hidden;
    position: absolute;
    left: 66.6%;
  }
  .st-img{
    height: 100%;
    width: 960px;
    position: absolute;
    left: -480px;
    margin-left: 150px;
    background-size: 100% 100%;
  }
  .nd-img{
    height: 100%;
    width: 768px;
    position: absolute;
    left: -384px;
    margin-left: 150px;
    background-size: 100% 100%;
  }
  .rd-img{
    height: 100%;
    width: 576px;
    position: absolute;
    left: -288px;
    margin-left: 150px;
    background-size: 100% 100%;
  }
  .bt {
    position: absolute;
    z-index: 2;
    width: 100%;
    padding-left: 85%;
    top:450px;
  }
  .xx{
    position: absolute;
    z-index: 5;
    /* width: 100%;
    height: 100%; */
  }
  #echarts{
    height: 540px;
  }
  canvas{
    display: none;
    position: absolute;
    left: 0;
    top: 0;
  }
</style>



