import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/home'
import Kiana from '@/components/kiana'
import Bronya from '@/components/bronya'
import Login from '@/components/login'
import Userinfo from '@/components/userinfo'

Vue.use(Router)

let router =  new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Hello
    },
    {
      path: '/bronya',
      name: 'bronya',
      component: Bronya
    },
    {
      path: '/kiana',
      name: 'kiana',
      component: Kiana,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/user/:id',
      name: 'userinfo',
      component: Userinfo,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  console.log(1)
  if (to.matched.some(record => record.meta.requiresAuth)) {
    console.log(2)
    let userId = document.cookie.replace(/(?:(?:^|.*;\s*)userId\s*\=\s*([^;]*).*$)|^.*$/, "$1")
    router.app.$store.commit('changecookie', userId)
    if (!userId) {
      document.cookie = 'userId=123'
      console.log(3)
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router



