<template>
  <div id="wrap">
    <h1>{{h1}}</h1>
    <h2>{{h2}}</h2>
    <Form ref="formValidate" 
      :model="formValidate" 
      :rules="ruleValidate" 
      :label-width="80"
    >
      <FormItem prop="name" label="帐号">
        <Input v-model="formValidate.name" placeholder="没有帐号会自动注册的说"></Input>
      </FormItem>
      <FormItem prop="passwd" label="密码">
        <Input type="password" placeholder="请勿使用与崩崩崩有关的密码哟" v-model="formValidate.passwd"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')">脱非入欧</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data() {
      return {
        h1:'舰长补给全保底',
        h2:'舰长副本零掉落',
        formValidate: {
          name:'',
          passwd:''
        },
        ruleValidata: {
          name:[
            {required:true,message:'帐号不能为空',trigger:'blur'}
          ]
        }
      }
    }
  }
</script>

<style scoped>
#wrap {
  position: absolute;
  left: 0;
  width: 320px;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -160px;
  margin-top: -160px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  z-index: 1;
}
#wrap h1{
  height: 42px;
  margin: 21px 0;
  font-size: 38px;
}

#wrap h2{
  height: 31px;
  margin: 20px 0;
  font-size: 25px;
}
</style>




